.navCategory {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  width: 150px;
  height: 50px;
  transition: 0.2s;
  cursor: pointer;
}

.navCategory:hover {
  background-color: #292a48;
}

.navCategory.active {
  background-color: #292a48;
}

.navCategory span {
  font-family: "Proxima Nova Th";
  font-weight: bold;
  font-size: 14px;
  color: #8a8995;
  margin-right: 14px;
}

.navCategory__info {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-left: 15px;
}

.navCategory__info > img {
  margin-right: 10px;
  margin-bottom: 1px;
}

.navCategory__info p {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: #fff;
  font-weight: 400;
}

.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 150px;
  height: 500px;
  background-color: transparent;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.sidebar:after {
  position: absolute;
  content: "";
  background-color: #101022;
  opacity: 0.85;
  width: 150px;
  height: 500px;
  top: 0;
  left: 0;
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.sidebar__header {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  height: 40px;
  padding-left: 15px;
  z-index: 1410;
}

.sidebar__nav {
  z-index: 1410;
}

.staricon {
  transition: 0.2s;
  cursor: pointer;
}

.staricon.filled:hover {
  opacity: 0.15;
}

.issueItem {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  width: 465px;
  border: 1px solid #e0e0e0;
  border-radius: 5px;
  background-color: #fff;
  margin-top: 5.5px;
  margin-bottom: 5.5px;
  padding: 0 10px;
}

.issueItem__title {
  font-family: "Roboto", sans-serif;
  font-size: 15px;
  font-weight: 400;
  margin-top: 2px;
}

.issueItem__data {
  font-family: "Roboto", sans-serif;
  font-size: 12px;
  color: #8a8995;
}

.issuesList {
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  margin: 8px 13px 0 15px;
  padding-right: 13px;
}

.mainframe {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 500px;
  height: 500px;
  background-color: #f6f6f6;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #e0e0e0;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #e8e8e8;
}

body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-image: url(/static/media/background.0f70c6b9.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  object-fit: cover;
}

.app {
  display: flex;
  flex-direction: row;
  width: 650px;
  height: 500px;
  -webkit-filter: drop-shadow(0 0 7.5px rgba(0, 0, 0, 0.5));
          filter: drop-shadow(0 0 7.5px rgba(0, 0, 0, 0.5));
}

